import React, {Component} from 'react';
import Visualizer from './Visualizer/Visualizer'
//import DropDownButton from './Button/DropDownButton'
import ImageSelectMenu from './SelectMenu/ImageSelectMenu'
import SelectMenu from './SelectMenu/SelectMenu'
//import './SortingVisualizer.css';

//state: algorithm, speed, datasize, (time?)

export default class SortingVisualizer extends Component {
  constructor(props) {
    super(props);
    this.state = {
        progress: 0,
        simulating: false
    };
    this.algs = ["Bubble Sort", "Quick Sort", "Merge Sort"];
    this.algImages = ["https://i.imgur.com/83vTj7A.png", "data:image/png;base64,", "https://i.imgur.com/JDlakf8.png"];
    this.speeds = ["1ms", "10ms", "50ms", "100ms"];
    this.delays = [1, 10, 50, 100];
    this.sizes = [10, 50, 100, 500];
    this.algBtnRef = React.createRef();
    this.sizeBtnRef = React.createRef();
    this.speedBtnRef = React.createRef();
    this.visRef = React.createRef();
  }

  handleOutsideClick(e) {
    if (!e.target.closest(".a")) {
      this.algBtnRef.current.close();
    }
    if (!e.target.closest(".b")) {
      this.sizeBtnRef.current.close();
    }
    if (!e.target.closest(".c")) {
      this.speedBtnRef.current.close();
    }
  }

  randomizeVisualizer() {
    if (!this.visRef.current.state.isRandomizing) {
      setTimeout(() => {
      const numBars = this.sizes[this.sizeBtnRef.current.state.selected];
      this.visRef.current.setNumBars(numBars);
      this.visRef.current.randomize();
      }, 0);
    }
  }

  startClick() {
    if (!this.visRef.current.isSimulating) {
      this.visRef.current.setState({isSimulating: true});
      const alg = this.algBtnRef.current.state.selected;
      const delay = this.speedBtnRef.current.state.selected;
      if (alg === 0) {
        this.visRef.current.animateBubbleSort(this.delays[delay]);
      }
      else if (alg === 1) {
        this.visRef.current.animateQuickSort(this.delays[delay]);
        console.log("done!");
      }
      else if (alg === 2) {
        this.visRef.current.animateMergeSort();
      }
    }
  }

  render() {
      return (
        <div className="absolute w-full h-full bg-white" onClick={(e) => this.handleOutsideClick(e)}>
          <div className="bg-blue-50 pt-3 h-1/6 shadow-md space-y-6">
            <div className="flex justify-evenly">
              <ImageSelectMenu ref={this.algBtnRef} labelText="Algorithm" trigger="a" options={this.algs} optionImages={this.algImages} defaultIndex={0} />
              <SelectMenu ref={this.sizeBtnRef} labelText="Data Size" trigger="b" options={this.sizes} defaultIndex={0} />
              <SelectMenu ref={this.speedBtnRef} labelText="Step Time" trigger="c" options={this.speeds} defaultIndex={0} />
              <div>
                <label id="listbox-label" className="block text-sm font-medium text-gray-700 tracking-wider select-none opacity-0">
                  easter
                </label>
                <button onClick={() => this.randomizeVisualizer()} type="button" aria-haspopup="listbox" aria-expanded="true" aria-labelledby="listbox-label" className="relative w-48 h-10 bg-white hover:bg-gray-200 border border-gray-300 rounded-md shadow-sm py-2 text-left cursor-pointer focus:outline-none sm:text-sm">
                  <span className="relative flex justify-center items-center font-medium ">
                    <span className="relative block truncate">
                      Randomize
                    </span>
                  </span>
                </button>
              </div>
              <div>
                <label id="listbox-label" className="block text-sm font-medium text-gray-700 tracking-wider select-none opacity-0">
                  egg
                </label>
                <button onClick={() => this.startClick()} type="button" aria-haspopup="listbox" aria-expanded="true" aria-labelledby="listbox-label" className="relative w-48 h-10 bg-white hover:bg-gray-200 border border-gray-300 rounded-md shadow-sm py-2 text-left cursor-pointer focus:outline-none sm:text-sm">
                  <span className="relative flex justify-center items-center font-medium select-none">
                    <span id="progressBar" className="absolute bg-green-100 rounded-md h-8 select-none" style={{width: `${11.5*this.state.progress/100}rem`}}></span>
                    <span className="relative block truncate">
                      Simulate
                    </span>
                  </span>
                </button>
              </div>
            </div>
            <div id="statusBar" className="z-0 select-none block text-sm font-medium text-gray-700 tracking-wider"></div>
          </div>
          <div className="flex text-white h-5/6 items-end justify-center pb-20">
            <Visualizer className="z-0" ref={this.visRef} defaultNumBars={this.sizes[0]} />
          </div>
        </div>
      );

  }
}