import React, {Component} from 'react';

//props: datasize, width, height

export default class Visualizer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      numBars: props.defaultNumBars,
      barHeights: [],
      isRandomizing: true,
      tempHeights: [],
      isSimulating: false
    };
  }
  
  randomInt(min, max) {
    return Math.floor(Math.random() * (max-min+1) + min);
  }

  componentDidMount() {
    this.randomize();
  }

  setNumBars(num) {
    this.setState({numBars: num});
  }

  /*async randomize() {
    this.setState({isRandomizing: true});
    //first show initially empty bars
    let bars = Array(this.state.numBars).fill(0);
    this.setState({barHeights: bars});


    //shuffle indices to initialize
    let indices = [];
    for (let i = 0; i < this.state.numBars; i++) {
      indices.push(i);
    }
    indices = this.shuffle(indices);
    
    const maxHeight = window.innerHeight * 2/3;
    const delay = 500 / this.state.numBars;

    //now animate bars becoming random heights
    for (let i = 0; i < this.state.numBars; i++) {
      bars[indices[i]] = this.randomInt(5, maxHeight);
      this.setState({barHeights: bars});
      await this.sleep(delay);
    }
    this.setState({isRandomizing: false});
  }*/

  randomize() {
    this.setState({isRandomizing: true});
    //first show initially empty bars
    let list = Array(this.state.numBars).fill(0);

    this.setState({barHeights: list});

    //shuffle indices to initialize
    let indices = [];
    for (let i = 0; i < this.state.numBars; i++) {
      indices.push(i);
    }
    indices = this.shuffle(indices);

    const maxHeight = window.innerHeight * 2/3;
    const delay = 1000 / this.state.numBars;
    
    //animate bars becoming random heights
    const bars = document.getElementsByClassName("visBar");

    for (let i = 0; i < this.state.numBars; i++) {
      list[indices[i]] = this.randomInt(5, maxHeight);
      setTimeout(() => {
        bars[indices[i]].style.height = `${list[indices[i]]}px`;
      }, i * delay);
    }
    setTimeout(() => {
      this.setState({isRandomizing: false, barHeights: list});
    }, this.state.numBars * delay);
  }

  shuffle(array) {
    let currentIndex = array.length, temporaryValue, randomIndex;
  
    // While there remain elements to shuffle...
    while (0 !== currentIndex) {
  
      // Pick a remaining element...
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex -= 1;
  
      // And swap it with the current element.
      temporaryValue = array[currentIndex];
      array[currentIndex] = array[randomIndex];
      array[randomIndex] = temporaryValue;
    }
  
    return array;
  }

  sleep(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  getProgress(list) {
    let count = 0;
    const sorted = this.state.barHeights.slice().sort((a, b) => a-b);
    //console.log(list);
    //console.log(sorted);
    for (let i = 0; i < this.state.numBars; i++) {
      if (list[i] === sorted[i]) {
        count++;
      }
    }
    //console.log(count/this.state.numBars);
    return count/this.state.numBars;
  }

  //every pair of indices is a swap
  getBubbleSortSwaps() {
    let list = this.state.barHeights.slice();
    let len = this.state.numBars;
    const orig = this.state.numBars;
    let swaps = [];
    //console.log(list);
    while(len > 1) {
      let newLen = 0;
      for (let i = 1; i < len; i++) {
        if (list[i-1] > list[i]) {
          newLen = i;
          //swap values
          const tmp = list[i-1];
          list[i-1] = list[i];
          list[i] = tmp;
          //add values to swaps array
          //swaps.push([i-1, i, this.getProgress(list)]);
          swaps.push([i-1, i]);
        }
      }
      len = newLen;
    }
    //console.log(list);
    return [list, swaps];
  }

  animateBubbleSort(delay) {
    const bars = document.getElementsByClassName("visBar");
    const result = this.getBubbleSortSwaps();
    const list = result[0];
    const swaps = result[1];

    for (let i = 0; i < swaps.length; i++) {
      const b1 = swaps[i][0];
      const b2 = swaps[i][1];
      setTimeout(() => {
        //make bars red
        bars[b1].style.background = "linear-gradient(#fecaca, #f87171)";
        bars[b2].style.background = "linear-gradient(#fecaca, #f87171)";
        //swap heights
        const tmp = bars[b1].style.height;
        bars[b1].style.height = bars[b2].style.height;
        bars[b2].style.height = tmp;
        //update progress bar
        document.getElementById("progressBar").style.width = `${11.5*i/swaps.length}rem`;
      }, i * delay);
      setTimeout(() => {
        bars[b1].style.background = "linear-gradient(#bfdbfe, #60a5fa)";
        bars[b2].style.background = "linear-gradient(#bfdbfe, #60a5fa)";
      }, (i+1) * delay);
    }
    setTimeout(() => {
      document.getElementById("progressBar").style.width = "0rem";
      this.setState({barHeights: list, isSimulating: false});
    }, swaps.length * delay);
  }

  quickSortPartition(list, lo, hi, swaps) {
    const pivot = list[Math.floor((hi+lo)/2)];
    let i = lo-1;
    let j = hi+1;
    while (true) {
      i++
      while (list[i] < pivot) {
        i++;
      }
      j--;
      while (list[j] > pivot) {
        j--;
      }
      if (i >= j) {
        return j;
      }
      //swap values
      const tmp = list[i];
      list[i] = list[j];
      list[j] = tmp;
      //add values to swaps array
      swaps.push([i, j]);
    }
  }

  quickSort(list, lo, hi, swaps) {
    if (lo < hi) {
      const p = this.quickSortPartition(list, lo, hi, swaps);
      this.quickSort(list, lo, p, swaps);
      this.quickSort(list, p+1, hi, swaps);
    }
    return [list, swaps];
  }

  getQuickSortSwaps() {
    let list = this.state.barHeights.slice();
    let len = this.state.numBars;
    //const orig = this.state.numBars;
    let swaps = [];
    return this.quickSort(list, 0, len-1, swaps);
  }

  animateQuickSort(delay) {
    const bars = document.getElementsByClassName("visBar");
    const result = this.getQuickSortSwaps();
    const list = result[0];
    const swaps = result[1];
    //const orig = this.state.numBars;

    for (let i = 0; i < swaps.length; i++) {
      const b1 = swaps[i][0];
      const b2 = swaps[i][1];
      setTimeout(() => {
        //make bars red
        bars[b1].style.background = "linear-gradient(#fecaca, #f87171)";
        bars[b2].style.background = "linear-gradient(#fecaca, #f87171)";
        //swap heights
        const tmp = bars[b1].style.height;
        bars[b1].style.height = bars[b2].style.height;
        bars[b2].style.height = tmp;
        //update progress bar
        document.getElementById("progressBar").style.width = `${11.5*i/swaps.length}rem`;
      }, i * delay);
      setTimeout(() => {
        bars[b1].style.background = "linear-gradient(#bfdbfe, #60a5fa)";
        bars[b2].style.background = "linear-gradient(#bfdbfe, #60a5fa)";
      }, (i+1) * delay);
    }
    setTimeout(() => {
      document.getElementById("progressBar").style.width = "0rem";
      this.setState({barHeights: list});
    }, swaps.length * delay);
  }

  animateMergeSort() {
    //const steps = mergeSort(array);
  }

  animateInsertionSort() {
    //const steps = insertionSort(array);
  }

  render() {
    let width;
    let padding;
    if (this.state.numBars === 10) {
      width = 100;
      padding = 10;
    }
    else if (this.state.numBars === 50) {
      width = 25;
      padding = 5;
    }
    else if (this.state.numBars === 100) {
      width = 15;
      padding = 3;
    }
    else {
      width = 2;
      padding = 1.75;
    }

    return (
      <>
        {this.state.barHeights.map((value, idx) => (
          <div className="visBar bg-gradient-to-t from-blue-400 to-blue-200 select-none shadow-md mr-0 rounded-sm"
              key={idx}
              style={{height: `${value}px`, width: `${width}px`, marginLeft: `${padding/2}px`, marginRight: `${padding/2}px`}}/>
        ))}
      </>
    );
  }
}